body {
  touch-action: pan-x pan-y;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.fullpage {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  background-color: black;
}

.reviewView {
  width: 45vh;
  height: 80vh;
  position: absolute;
  top: 1.25vh !important;
  left: calc(50vw - 22.5vh) !important;
  background-color: black;
}

.cropper {
  width: 45vh;
  height: 80vh;
  margin-top: 1.25vh !important;
  margin: auto;
  z-index: 8;
}

.cropper video {
  width: 45vh;
  height: 80vh;
  position: absolute;
  top: 1.25vh !important;
  left: calc(50vw - 22.5vh) !important;
  z-index: 0;
}

.cropper iframe {
  width: 45vh;
  height: 80vh;
  position: absolute;
  top: 1.25vh !important;
  left: calc(50vw - 22.5vh) !important;
  z-index: 10;
  background: 'transparent';
  stroke: none !important;
  border: none ! important;
}

.cropper a-scene {
  z-index: 10;
}

.interactBar {
  width: 80vw;
  height: 12.5vh;
  margin-top: 1.5vh !important;
  margin: auto;
  z-index: 4;
}

.interactBtn {
  width: max-content;
  height: fit-content;
  margin: auto;
}

.MuiPaper-elevation.MuiCard-root {
  box-shadow: 0px 2.5px 10px #1976d2 !important;
}

.jogo {
  position: fixed;
  z-index: 12;
  margin: 0 !important;
}

.info {
  top: 2vh;
  font-size: 125%;
  width: max-content;
  color: #fff;
  text-align: center !important;
  background: linear-gradient(to top,
      transparent,
      #00000069,
      transparent,
      transparent);
}

.esq {
  left: calc(50vw - 20vh);
}

.dir {
  right: calc(50vw - 20vh);
}

.centro {
  display: flex;
  width: 100vw;
  left: 0;
  top: 45vh;
  color: white;
  text-align: center;
}

.valJogo {
  top: 5vh;
  font-size: 200%;
  text-align: center !important;
  color: #fff;
  background: radial-gradient(circle closest-side,
      #d2ad19,
      transparent,
      #d2ad19,
      transparent,
      transparent);
}

.jogabilidade {
  width: 10vw;
}

#snackbar {
  visibility: hidden;
  /* Hidden by default. Visible on click */
  min-width: 250px;
  /* Set a default minimum width */
  margin-left: -125px;
  /* Divide value of min-width by 2 */
  color: #fff;
  /* White text color */
  text-align: center;
  /* Centered text */
  border-radius: 2px;
  /* Rounded borders */
  padding: 16px;
  /* Padding */
  position: fixed;
  /* Sit on top of the screen */
  z-index: 200;
  /* Add a z-index if needed */
  left: 50%;
  /* Center the snackbar */
  bottom: 0;
}

#snackbar.success {
  background-color: #1a7f57;
}

#snackbar.fail {
  background-color: #5a1010;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {
    bottom: -30px;
    opacity: 0;
  }

  to {
    bottom: 0px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: -30px;
    opacity: 0;
  }

  to {
    bottom: 0px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 0px;
    opacity: 1;
  }

  to {
    bottom: -30px;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 0px;
    opacity: 1;
  }

  to {
    bottom: -30px;
    opacity: 0;
  }
}
